*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body{
      width: 415px;
      margin: 0 auto;
  }
  
  .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #EFEFEF;
    padding: 15px 25px 18px 16px;
  }
  
  .title{
    font-size: 22px;
  }
  
  .burger{
    height: 2px;
    width: 20px;
    background: #000;
    position: relative;
  }

  .burger::after{
    content: '';
    height: 2px;
    width: 20px;
    background: #000;
    position: absolute;
    top: 5px;
  }

.main__block{
    background: #FAFAFA;
    height: 100%;
    padding: 69px 16px 0;
}
  

.sub__title{
    font-weight: 700;
    font-size: 19px;
    line-height: 130%;
    margin-bottom: 26px;
}



.card__title{
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: #05050E;
    margin-bottom: 15px;
}

.card__text{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 135%;
    color: #37383E;
    margin-bottom: 26px;
}

.main__content{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 24px;
}

.card{
    background: #FFFFFF;
    border: 2px solid #EFEFEF;
    border-radius: 20px;
    margin-bottom: 15px;
}

.card__footer{
    padding: 14px;
    border-top: 2px solid #EFEFEF;
    display: flex;
    justify-content: space-between;
}

.card__info, .card__lesson{
    display: flex;
}

.card__progress, .card__tag{
    background: #F4F4F4;
    border-radius: 10px;
    font-weight: 700;
    font-size: 12px;
    line-height: 130%;
    color: #05050E;
}

.card__progress{
    padding: 6px 13px 7px 18px;
    
}

.card__tag{
    width: 33px;
    height: 29px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
}

.card__lesson{
    align-items: center;
}

.card__lesson--text{
    margin-left: 6px;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    color: #707175;
}

.card__lock{
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background: #E2E2E2;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 26px;
}

.card__closed{
    background: #FBFBFB;
    opacity: 0.6;
}



.header__app{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 2px solid #EFEFEF;
    padding: 15px 25px 18px 16px;
}

.header__title{
    font-weight: 700;
    font-size: 16px;
    line-height: 130%;
    color: #05050E;
    margin-left: 20px;
}

.progres{
    padding: 15px 15px 20px;
    border-bottom: 2px solid #EFEFEF;
}

.progres__text{
    display: flex;
    justify-content: space-between;
}


.progres__line{
    width: 381px;
    height: 8px;
    background: #EDEDED;
    margin-top: 16px;
}

.main__course{
    background: #FAFAFA;
    height: 100%;
    padding: 33px 16px 0;
}

.lesson__card, .lesson__card--closed{
    padding: 24px 21px 25px 25px;
    background: #fff;
    border: 2px solid #EFEFEF;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
}

.lesson__card--closed{
    background: #F1F1F1;
    border: 2px solid #E3E3E3;
}

.lesson__text{
    display: flex;
    align-items: center;
}

.lesson__text > .card__lock{
    margin-top: 0;
    margin-right: 15px;
}

.modal{
    background: #F26438;
    border-radius: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 14px;
    position: relative;
}

.modal.active{
    display: flex;
}


.modal::after {
    content: ''; 
    position: absolute; /* Абсолютное позиционирование */
    left: 46%; 
    top: -40px; /* Положение треугольника */ /* Добавляем треугольник */
    border: 20px solid transparent;
    border-bottom: 20px solid #F26438;
   }

.btn{
    width: 100%;
    border-radius: 15px;
    font-size: 16px;
    line-height: 130%;
    text-align: center;
    padding: 15px 0 16px;
    font-weight: 900;
    text-decoration: none;
}

.btn__theory{
    background: #E35C31;
    border: 3px solid #F26438;
    color: #F7F7F8;
    margin-bottom: 8px;
}

.btn__lesson{
    background: #F7F7F7;
    color: #0E0E16;
    border: none;
    
}


.theory__wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px;
    gap: 31px;
}


.theory__lesson{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px;
    gap: 22px;
}

.theory__lesson--block{
    display: flex;
    gap: 6px;
}

.theory__img{
    width: 100px;
    height: 100px;
}


.btn__lesson--orange{
    padding: 15px 40px 25px;
    gap: 6px;
    background: #F26438;
    border: 3px solid #C4694C;
    color: #F7F7F8;
    width: 200px;
    height: 60px;
}

.btn__lesson--width{
    width: 100%;
    display: block;
}


.theory__img__wrapper{
    background: #EDEDED;
    border-radius: 20px;
    padding: 16px;
}

.theory__wrapper{
    border-bottom: 1px solid #EDEDED;
    padding: 35px;
}

.theory__content{
    background: #F7F7F8;
    padding: 20px;
}

.theory__text{
    color: #05050E;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 30px;
}

.theory__title{
    color: #05050E;
    font-style: normal;
    font-weight: 900;
    font-size: 18px;
    line-height: 130%;
    margin: 37px 0 33px;
}


.theory__text--orange{
    color: #f26438;
}

.theory__list{
    margin-left: 20px;
    color: #05050E;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 30px;
}


/* Quiz.css */

.btn__cross{
    height: 38px;
    width: 38px;
    border-radius: 50px;
    background: #F2F2F2;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cross{
    display: block;
    width: 12px;
    height: 1px;
    background: #454545;
    border-radius: 50px;
    transform: rotate(45deg);
}
.cross::after{
    content: '';
    display: block;
    width: 12px;
    height: 1px;
    background: #454545;
    border-radius: 50px;
    transform: rotate(90deg);
}

.quiz{
    position: relative;
}

.quiz__header{
    background: #FBFBFB;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.proggress__bar{
    width: 100%;
    height: 8px;
    background: #EDEDED;
    border-radius: 90px;
    margin-left: 16px;
}

.proggress__now{
    width: 2%;
    height: 8px;
    border-radius: 90px;
    background: #F26438;
}

.quiz__question{
    margin: 15px auto 30px;
    width: 280px;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    text-align: center;
    color: #05050E;
}


.quiz__zone{
    background: #f7f7f8;
    padding: 15px;
    
}

.quiz__radio__zone{
    display: flex;
    flex-direction: column;
    gap: 7px;
    margin: 25px 0 20px;
}

.quiz__radio{
    padding: 22px 22px 27px;
    border: 1px solid #EDEDED;
    border-radius: 15px;    
    background: #fff;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    color: #686873;
}

.btn__quiz{
    background: #F26438;
    border: 3px solid #C4694C;
    color: #F7F7F8;
}

.btn__quiz:disabled{
    color: #B0B0B0;
    background: #D4D4D4;
    border: 3px solid #CECECE;
}